import { Platform } from 'react-native';
import { checkPhoneSize } from './checkPhoneSize';

/**
 * 判断是否是iphone X系列
 */
export function isIphoneX() {
    return (
        Platform.OS === 'ios' &&
        !Platform.isPad &&
        !Platform.isTVOS &&
        (checkPhoneSize(780) ||
            checkPhoneSize(812) ||
            checkPhoneSize(844) ||
            checkPhoneSize(896) ||
            checkPhoneSize(926) ||
            checkPhoneSize(852) ||
            checkPhoneSize(932))
    );
}
